// components/features/home/CharitiesList.tsx (REVISED)

'use client';

import { useState, useMemo } from 'react';
import { Box, TextField, Typography } from '@mui/material';
import { Search } from '@mui/icons-material'; // Only need Search icon now
import { colors } from '@/theme';
import type { Charity } from 'lib/charities';
import Greeting from './Greeting';
import CharityCard from './CharityCard';
import text from '@/content/text.json';
// ðŸš¨ NEW IMPORTS
import CategoryChips, { CATEGORY_ICONS, CATEGORIES, type CategoryName } from './';

// Removed the CATEGORY_ICONS object and icon imports here

// Removed the CategoryName type and CATEGORIES array export here

interface CharitiesListProps {
  charities: Charity[];
}

const Home: React.FC<CharitiesListProps> = ({ charities }) => {
  const [filter, setFilter] = useState('');
  // Use the imported CategoryName type
  const [selectedCategory, setSelectedCategory] = useState<CategoryName | null>(null);

  const filtered = useMemo(() => {
    return charities.filter(charity => {
      // The logic still relies on CategoryName, so we keep the type assertion safe
      const isKnownCategory = CATEGORIES.includes(charity.category as CategoryName);

      const matchesCategory = selectedCategory ? charity.category === selectedCategory : true;
      const matchesFilter =
        charity.title.toLowerCase().includes(filter.toLowerCase()) ||
        charity.category.toLowerCase().includes(filter.toLowerCase()) ||
        charity.description.toLowerCase().includes(filter.toLowerCase());
      return matchesCategory && matchesFilter;
    });
  }, [charities, filter, selectedCategory]);

  return (
    <Box sx={{ display: 'flex', flexDirection: 'column', height: '100vh', overflow: 'hidden' }}>
      <Box
        sx={{
          p: { xs: 2, sm: 3, md: 4 },
          display: 'flex',
          flexDirection: 'column',
          gap: 2,
          backgroundColor: colors.secondary,
          position: 'sticky',
          top: 0,
          zIndex: 1000,
        }}
      >
        <Greeting />
        <TextField
          label=""
          placeholder={text.home.search}
          variant="filled"
          fullWidth
          value={filter}
          onChange={e => setFilter(e.target.value)}
          slotProps={{
            input: {
              startAdornment: (
                <Search fontSize="small" sx={{ color: colors.secondaryTextLight, mr: 1 }} />
              ),
            },
          }}
          sx={{
            backgroundColor: colors.secondaryLight,
            borderRadius: 2,
            '& .MuiFilledInput-root': {
              backgroundColor: colors.secondaryLight,
              borderRadius: 2.5,
              padding: '10px 12px',
              '&:before, &:after': { display: 'none' },
            },
            '& .MuiInputBase-input::placeholder': {
              color: colors.secondaryTextLight,
              opacity: 1,
            },
            // ðŸš¨ Apply padding fixes here if you removed them
            '& .MuiFilledInput-input': {
              paddingTop: '0 !important',
              paddingBottom: '0 !important',
              paddingLeft: '0 !important',
              paddingRight: '0 !important',
            },
          }}
        />
        {/* ðŸš¨ REPLACE WITH NEW COMPONENT */}
        <CategoryChips
          selectedCategory={selectedCategory}
          setSelectedCategory={setSelectedCategory}
        />
      </Box>
      <Box sx={{ flex: 1, overflowY: 'auto', p: { xs: 2, sm: 3, md: 4 } }}>
        <Box sx={{ display: 'flex', flexDirection: 'column', gap: 2 }}>
          {filtered.map(charity => (
            <CharityCard key={charity.id} charity={charity} />
          ))}
        </Box>
        {filtered.length === 0 && (
          <Box sx={{ textAlign: 'center', py: 8 }}>
            <Typography variant="h6" color="text.secondary">
              No charities found
            </Typography>
            <Typography variant="body2" color="text.secondary" sx={{ mt: 1 }}>
              Try adjusting your search
            </Typography>
          </Box>
        )}
      </Box>
    </Box>
  );
};

export default Home;
